<template>
  <label class="form-group">
    <textarea
      class="form-group__control"
      placeholder=" "
      :name="name"
      v-text="value"
      @input="onInput"
    ></textarea>

    <div class="form-group__label" v-if="label">{{ label }}</div>
  </label>
</template>

<script>
export default {
  props: {
    name: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      default: ''
    },
    label: {
      type: String
    }
  },
  methods: {
    onInput (e) {
      this.$emit('input', e.target.value)
      e.target.style.height = '1px'
      e.target.style.height = `${e.target.scrollHeight + 5}px`
    }
  }
}
</script>

<style scoped>
  .form-group {
    position: relative;
    border: 1px solid #ddd;
    border-radius: 2px;
    display: block;
    box-sizing: border-box;
    transition: border-bottom-color .2s;
  }
  .form-group:focus-within {
    border: 0;
    border-bottom: 2px solid hsl(var(--ui-active));
  }
  .form-group__control {
    width: 100%;
    height: 30px;
    max-height: 600px;
    outline: none;
    border: none;
    resize: none;
    margin-top: 15px;
    box-sizing: border-box;
    padding: 6px 15px 0 15px;
    font-size: 17px;
    font-family: inherit;
    color: hsl(var(--ui-primary-h), 20%, var(--ui-primary-l));
    transition-property: border-color, padding, color;
    transition-duration: .2s;
  }
  .form-group__control:focus {
    color: hsl(var(--ui-primary));
    padding: 5px 15px 0 0;
    border-radius: 0;
  }
  .form-group__label {
    position: absolute;
    top: 14px;
    left: 15px;
    font-size: 17px;
    cursor: text;
    color: hsl(var(--ui-primary-h), 20%, var(--ui-primary-l));
    transition: left .2s, top .2s, color .2s, font-size .2s;
  }
  .form-group__control:focus ~ .form-group__label {
    top: 5px;
    left: 0;
    font-size: 11px;
    color: hsl(var(--ui-active));
  }
  .form-group__control:not(:placeholder-shown) ~ .form-group__label {
    top: 5px;
    font-size: 11px;
  }
</style>